<script>
    const defaultOption = {
        data: {
            // 总计
            total   : 326.63,
            // 百分比
            progress: 2.31
        }
    }

    const defaultConfig = {
        // 标题
        label           : '今日进气量(万m³)',
        // 百分比标题
        subLabel        : '瞬时进气量(m³/h)',
        // 容器样式
        wrapperStyle    : {},
        // 总计保留小数
        totalDecimals   : 2,
        // 总计动画时间
        totalDuration   : 2,
        // 百分比保留小数
        progressDecimals: 2,
        // 百分比动画时间
        progressDuration: 2
    }

    import Animation from "@/js/animation";
    export default {
        name      : "page-title",
        // mixins    : [getData],
        props     : {
            styleObject: {
                type: Object,
                default() {
                    return {}
                }
            },
            option     : {
                type: Object,
                default() {
                    return {}
                }
            }
        },
        data() {
            return {
              // 持续时间
                duration      : 2,
                startVal:0,
                // 保留位数
                decimals      : 2,
                //起始旋转角度
                rotateStartVal: 0,
                // 旋转角度
                rotate: 1080,
                // 持续时间
                rotateDuration: 4,
                // 保留位数
                rotateDecimals: 0.01,
                _animation:null,
                ready:false
            }
        },
        mounted() {
          this.ready = true;
          setTimeout(()=>{
              this._animation = new Animation(
                this.rotateStartVal,
                this.rotate,
                this.rotateDecimals,
                this.rotateDuration,
                {
                  printValue: val => {
                    this.$refs.rotate.setAttribute(
                      "transform",
                      `rotate(-${val})`
                    );
                  }
                }
              );
              this._animation.start();
          },800)
        },
        computed  : {
            newStyle() {
                const style = this.style || {}
                const styleObject = this.option.wrapperStyle || {}
                return {...style, ...styleObject}
            }
        },
        watch     : {
            // 进度值
            progress(val) {
                this._progressCountup.update(val)
            },
            total(val) {
                this._totalCountUp.update(val)
            },
            chartData  : {
                handler(data = {}) {
                    const {total, progress} = data
                    this.total = total || 0
                    this.progress = progress || 0
                },
                immediate: true
            },
            chartConfig: {
                handler(data = {}) {
                    const config = {...defaultConfig, ...data}
                    const {
                        label,
                        subLabel,
                        totalDecimals,
                        totalDuration,
                        progressDecimals,
                        progressDuration
                    } = config

                    this.label = label || ''
                    this.subLabel = subLabel || ''
                    this.totalDecimals = totalDecimals || 2
                    this.totalDuration = totalDuration || 2
                    this.progressDecimals = progressDecimals || 2
                    this.progressDuration = progressDuration || 2
                },
                immediate: true
            }
        },
        components: {
            // DashboardPanel
        }
    }
</script>

